@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: medium;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.link {
  text-decoration: none;
  color: inherit;
}
.img {
  display: block;
  border: none;
  outline: none;
}
.page {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  min-width: 320px;
  height: 100%;
}
.page__body {
  margin: 0;
  min-height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
.br-tablet {
  display: none;
}
@media (min-width: 768px) {
  .br-tablet {
    display: inline;
  }
}
.br-desktop {
  display: none;
}
@media (min-width: 1240px) {
  .br-desktop {
    display: inline;
  }
}
.br-mobile {
  display: inline;
}
@media (min-width: 768px) {
  .br-mobile {
    display: none;
  }
}
.not-wrap-text {
  white-space: nowrap;
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  width: 280px;
  height: 40px;
  background-color: #68B738;
}
.button:hover {
  background-color: #5EAA2F;
}
.button--secondary {
  color: #000000;
  background-color: #F2F2F2;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary:hover {
  color: #000000;
  background-color: #EBEBEB;
}
.button--secondary:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-header {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1220px;
  margin: 0 auto 0 auto;
  box-sizing: border-box;
  width: 100%;
}
@media (min-width: 768px) {
  .main-header {
    flex-direction: row;
    justify-content: space-between;
    height: 52px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1240px) {
  .main-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: flex-end;
    height: 112px;
    padding: 0;
    margin: auto;
    padding-top: 5px;
  }
}
.main-header__logo {
  position: relative;
  display: grid;
  grid-template-columns: 33px 1fr;
  height: 65px;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  padding-left: 20px;
  padding-right: 53px;
}
@media (min-width: 768px) {
  .main-header__logo {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    max-width: 174px;
    border-bottom: none;
    height: 52px;
    padding-left: 0;
    padding-right: 93px;
  }
}
@media (min-width: 1240px) {
  .main-header__logo {
    max-width: max-content;
    height: 56px;
  }
}
.logo__title {
  margin-left: auto;
  margin-right: auto;
  flex-grow: 1;
}
.logo__title::after {
  content: "";
  display: block;
  width: 101px;
  height: 18px;
  background-image: url("../img/logo/stack.svg#logo-title");
}
@media (min-width: 768px) {
  .logo__title {
    display: none;
  }
}
.logo__img {
  position: relative;
}
.logo__img::after {
  content: "";
  display: block;
  width: 33px;
  height: 38px;
  background-image: url("../img/logo/stack.svg#logo-mobile");
}
@media (min-width: 768px) {
  .logo__img::after {
    width: 174px;
    height: 52px;
    background-image: url("../img/logo/stack.svg#logo-tablet");
  }
}
@media (min-width: 1240px) {
  .logo__img::after {
    width: 202px;
    height: 59px;
  }
}
@media (min-width: 1240px) {
  .logo__img::before {
    content: "";
    position: absolute;
    display: block;
    width: 68px;
    height: 59px;
    background-image: url("../img/logo/stack.svg#logo-desktop");
  }
}
.logo__title:hover,
.logo__img:hover {
  opacity: 80%;
}
.logo__title:active,
.logo__img:active {
  opacity: 60%;
}
.main-nav {
  position: relative;
  display: flex;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
  }
}
/*СТИЛИ СПИСКА МЕНЮ ПО УМОЛЧАНИЮ*/
.main-nav__list.main-nav__list--nojs {
  position: static;
}
.main-nav__list.main-nav__list--js {
  position: absolute;
}
@media (min-width: 1240px) {
  .main-nav__list.main-nav__list--js {
    top: -56px;
  }
}
.main-nav__list,
.main-nav__list.main-nav__list--nojs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  margin-top: -1px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  background-color: #ffffff;
  z-index: 5;
  border-top: 1px solid hsl(0, 0%, 90%);
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list,
  .main-nav__list.main-nav__list--nojs {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    border-top: none;
  }
}
@media (min-width: 1240px) {
  .main-nav__list,
  .main-nav__list.main-nav__list--nojs {
    font-size: 20px;
    line-height: 30px;
    width: 480px;
    background-color: transparent;
    padding-left: 130px;
    padding-right: 0;
  }
}
@media (min-width: 1240px) {
  .main-nav__list--index {
    color: #ffffff;
  }
}
.main-nav__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}
/*СТИЛИ ПУНКТА МЕНЮ*/
.main-nav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 65px;
  width: 100%;
  border-bottom: 1px solid hsl(0, 0%, 90%);
}
@media (min-width: 768px) {
  .main-nav__item {
    height: 38px;
    padding-top: 12px;
    border-bottom: none;
    align-items: center;
    width: auto;
  }
}
@media (min-width: 768px) {
  .main-nav__item--active {
    border-bottom: 2px solid #68B738;
  }
}
@media (min-width: 1240px) {
  .main-nav__item--active-index {
    border-bottom: 2px solid #ffffff;
  }
}
.main-nav__item:hover {
  opacity: 60%;
}
.main-nav__item:active {
  opacity: 30%;
}
/*JS*/
.main-nav__toggle {
  position: absolute;
  top: -45px;
  left: calc(100% - 43px);
  display: block;
  align-items: center;
  padding: 0;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 3;
}
.main-nav__toggle::after {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  mask: url("../img/icons/stack.svg#open-menu");
  background-color: #000000;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--close::after {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  mask: url("../img/icons/stack.svg#close-menu");
  background-color: #000000;
}
.main-nav__toggle.main-nav__toggle--nojs {
  display: none;
}
.main-nav__toggle:hover {
  opacity: 60%;
}
.main-nav__toggle:active {
  opacity: 30%;
}
.footer {
  position: relative;
  background-color: #F2F2F2;
}
.footer__cooperation-title {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .footer__cooperation-title {
    font-size: 20px;
    line-height: 26px;
  }
}
.footer__cooperation {
  display: flex;
  padding: 26px 20px;
  background-color: #F2F2F2;
  justify-content: space-around;
  gap: 20px;
}
@media (min-width: 768px) {
  .footer__cooperation {
    justify-content: space-between;
    padding: 58px 70px;
  }
}
@media (min-width: 1240px) {
  .footer__cooperation {
    position: absolute;
    padding: 56px 80px 56px 80px;
    width: 405px;
    height: 86px;
    align-items: center;
    top: 95px;
    left: 50.5%;
    transform: translateX(-110%);
    z-index: 1;
    background-color: #ffffff;
  }
}
.footer__cooperation.footer__cooperation--index {
  background-color: #ffffff;
}
.footer__cooperation-address {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #222222;
  font-style: normal;
}
@media (min-width: 768px) {
  .footer__cooperation-address {
    width: min-content;
    font-size: 16px;
    text-align: end;
  }
}
@media (min-width: 1240px) {
  .footer__cooperation-address {
    text-align: left;
    margin-top: -6px;
  }
}
@media (min-width: 768px) {
  .footer__cooperation-address-city {
    display: inline-block;
    margin-top: 20px;
  }
}
.footer__location {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.map {
  position: relative;
}
.map__pointer {
  width: 57px;
  height: 53px;
  position: absolute;
  top: 52%;
  left: 51%;
  transform: translate(-50%, -100%);
  object-fit: fill;
}
@media (min-width: 768px) {
  .map__pointer {
    width: 113px;
    height: 106px;
    top: 62%;
  }
}
@media (min-width: 1240px) {
  .map__pointer {
    top: 60%;
    left: calc(51% + 230px);
  }
}
.map__google {
  width: 100%;
  height: 362px;
}
@media (min-width: 768px) {
  .map__google {
    height: 400px;
  }
}
.footer-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
