  border-bottom: 1px solid hsl(0, 0%, 85%);
  margin: 0;
  box-sizing: border-box;
  padding-top: 22px;
  padding-bottom: 18px;
  padding-left: calc((100% - 128px) / 2);
  padding-right: calc((100% - 128px) / 2);
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .social-list {
    padding: 0;
