}
.social-list__item :active {
  opacity: 30%;
}
.hiro-block {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: max-content 1fr min-content;
  font-weight: 400;
  color: #ffffff;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .hiro-block {
    padding-left: 120px;
    padding-top: 64px;
    padding-right: 30px;
    max-width: 1070px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 768px) and (max-width: 1239px) {
  .hiro-block {
    min-height: 790px;
  }
}
@media (min-width: 1240px) {
  .hiro-block {
    grid-template-columns: 50vw 50vw;
    margin-top: -112px;
    min-height: 696px;
    max-width: 100%;
    padding: 0;
  }
}
.hiro-block__title {
  text-align: center;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 25px;
  max-width: 280px;
}
@media (min-width: 768px) {
  .hiro-block__title {
    font-size: 60px;
    line-height: 60px;
    color: #000000;
    text-align: start;
    margin-bottom: 0;
    max-width: 416px;
  }
}
@media (min-width: 1240px) {
  .hiro-block__title {
    margin-top: -7px;
    max-width: 445px;
  }
}
.hiro-block__text {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .hiro-block__text {
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    flex-grow: 0;
    margin-top: 40px;
    margin-bottom: auto;
  }
}
.hiro-block__background-holder {
  position: relative;
  grid-row-start: 1;
  grid-row-end: -1;
  grid-column-start: 1;
  grid-column-end: 2;
  height: 302px;
  background-image: url("../img/hiro-block-background-@1.webp");
  background-size: max(580px, 100%);
  background-position: 63% 51%;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .hiro-block__background-holder {
    background-image: url("../img/hiro-block-background-@2.webp");
  }
}
@media (min-width: 768px) {
  .hiro-block__background-holder {
    display: none;
  }
}
@media (min-width: 1240px) {
  .hiro-block__background-holder {
    display: block;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
    height: auto;
    background-position: 0 0;
    background-size: 1050px;
    background-color: #3e3d41;
  }
}
.hiro-block__background-holder::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 302px;
  background-color: #68B738;
  opacity: 85%;
}
@media (min-width: 1240px) {
  .hiro-block__background-holder::after {
    height: 100%;
  }
}
.hiro-block__content {
  position: relative;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
